





















































































































.title {
  padding: 0 30px;
  padding-top: 20px;
  font-size: 28px;
  color: #333;
}

.most-expected {
  position: relative;
  padding: 0 30px;
  padding-bottom: 20px;
  border-bottom: 20px solid #f5f5f5;
  .title {
    padding-left: 0;
    margin-bottom: 20px;
  }
  .expected-item {
    display: inline-block;
    width: 170px;
    overflow: hidden;
    margin-right: 20px;
  }
  .poster {
    position: relative;
    width: 170px;
    height: 230px;
    margin-bottom: 12px;
  }

  .name {
    margin-bottom: 3px;
    font-size: 24px;
    color: #333;
  }

  .data {
    font-size: 24px;
    color: #999;
  }
}
