

















































.movie-section {
  position: relative;
  display: flex;
  align-items: center;
  height: 228px;
  padding: 0 30px;
  &::after {
    position: absolute;
    content: "";
    height: 0;
    width: calc(100% - 208px);
    right: 30px;
    bottom: 0;
    border-bottom: 1px solid #f0f0f0;
  }
  .movie-img {
    width: 128px;
    height: 180px;
  }
  .movie-info {
    margin: 0 20px;
    font-size: 26px;
    color: #666;
    width: 60%;
    div {
      margin-bottom: 6px;
    }
  }
  .movie-name {
    font-size: 34px;
    color: #333;
    padding-right: 10px;
    .version {
      display: inline-block;
      height: 28px;
      background-size: contain;
      background-repeat: no-repeat;
      margin-left: 10px;
      margin-top: 10px;
    }
  }
}

.v3d.imax {
  width: 86px;
  background-image: url(data:image/png;base64,);
}

.v3d {
  width: 34px;
  background-image: url(data:image/png;base64,);
}

.v2d.imax {
  width: 86px;
  background-image: url(data:image/png;base64,);
}

.grade {
  font-weight: 700;
  color: #faaf00;
  font-size: 30px;
}
